import { useState } from 'react'
import { Link } from 'react-router-dom'
import { useForm } from "react-hook-form";
import classNames from 'classnames';
import MainAuth from '../components/MainAuth';
import styles from './SignIn.module.scss';
import Checkbox from '../../../components/ui/Checkbox';
import Body from '../../../containers/body';
import Button from '../../../components/ui/Button';

type FormData = {
  email: string;
  password: string;
  checkbox: boolean
};

const SignIn = () => {
  const [showPass, setShowPass] = useState(false)
  const { register, handleSubmit, formState: { errors } } = useForm<FormData>({
    mode: 'onTouched',
    defaultValues: {
      email: '',
      password: '',
      checkbox: true
    }
  });
  const onSubmit = handleSubmit(data => console.log(data));

  return (
    <Body>
      <MainAuth
        link='/sign-up'
        text={{
          title: '   Unite-Gaming',
          footnote: ' ?',
          link: ''
        }} >
        <form onSubmit={onSubmit} className={styles.form} noValidate>
          <label className='label'>
            Email
            <input
              type='email'
              className={classNames('input', { 'erorr': errors.email })}
              placeholder='   '
              {...register('email', { required: '   ' })} />
              {errors.email && <span className='error-message'>{errors.email.message}</span>}
          </label>
          <label className='label'>
            
            <input
              className={classNames('input', { 'erorr': errors.password })}
              type={showPass? 'text' : 'password'}
              placeholder=' '
              {...register('password', { required: ' ' })} />
              {errors.password && <span className='error-message'>{errors.password.message}</span>}
              <button
              type='button'
              className={classNames(styles.buttonShowPass, {[styles.active]: showPass})}
              onClick={() => setShowPass(!showPass)} />
          </label>
          <div className={styles.wraper}>
            <Checkbox register={register}>
              <p> </p>
            </Checkbox>
            <Link className={styles.forget} to='/forgot'> ?</Link>
          </div>
          <Button title='  ' fullPage={true} />
        </form>
      </MainAuth>
    </Body>
  )
}

export default SignIn